<template>
    <div>
        <van-nav-bar
                title="每天一个好心情"
                left-text="返回"
                left-arrow
                @click-left="onClickLeft"
                class="clock_in"
        />
        <van-cell-group>
            <van-cell v-for="(item,index) in tx_img" :key="index">
                <!-- 使用 title 插槽来自定义标题 -->
                <template #icon>
                    <van-image width="100" height="100" fit="cover" :src="item.mts_pic"/>
                </template>
                <template #title>
                    <span class="custom-title">{{item.mts_content}}</span><br>
                    <span class="custom-title">{{item.mts_time2.split(' ')[0]}}</span>
                </template>
            </van-cell>
        </van-cell-group>
    </div>
</template>

<script>
    export default {
        name: "Punch_in_record",
        data(){
          return{
              tx_img:[]
          }
        },
        mounted() {
            this.$axios.get('https://zhuzuoji.com/myapi/moments/findmymoments',{
                params:{
                    key:'vv9544',
                    email:this.$getCookie(' email')
                }
            }).then(res=>{
                if(res.status==200&&res.data.state){
                    this.tx_img=res.data.moments
                }else {
                    this.$toast.fail('查询失败')
                }

            }).catch(err=>{
                let error=err.toString()
                if(error.indexOf('Network')!=-1){
                    this.$toast.fail('请检查网络')
                }
            })
        },
        methods:{
            onClickLeft(){
                this.$router.push({
                    path:'/',
                    query:{active:2}
                })
            }
        }

    }
</script>

<style scoped>
    .clock_in {
        background-color: rgb(146,188,255);
        position: fixed;
        width: 100%;
        height: 46px;
        top: 0;
    }
    .van-cell-group{
        margin-top: 46px;
    }
    .custom-title{
        margin-left: 10px;
    }
    >>>.van-nav-bar__text{
        color: #fff;
    }
    >>>.van-icon::before{
        color: #fff;
    }
    >>>.van-nav-bar__title{
        color: #fff;
    }
</style>